import tuiTabs from "@/components/tui-tabs/tui-tabs"
import tuiButton from "@/components/extend/button/button"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
import tuiListCell from "@/components/list-cell/list-cell"
import tuiSticky from "@/components/sticky/sticky"
export default {
	components: {
		tuiTabs,
		tuiButton,
		tuiLoadmore,
		tuiNomore,
		tuiListCell,
		tuiSticky
	},
	data() {
		return {
			tabs: [{
				name: "全部"
			}, {
				name: "待付款"
			}, {
				name: "待发货"
			}, {
				name: "待收货"
			}, {
				name: "待评价"
			}],
			currentTab: 0,
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			scrollTop: 0
		}
	},
	methods: {
		change(e) {
			this.currentTab = e.index
		},
		detail(){
			this.tui.toast("功能尚未完善~")
		}
	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200);
	},
	onReachBottom() {
		//只是测试效果，逻辑以实际数据为准
		this.loadding = true
		this.pullUpOn = true
		setTimeout(() => {
			this.loadding = false
			this.pullUpOn = false
		}, 1000)
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	}
}
